/*   Loan Default    */


.LoanDefaultInputsContainer {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 1.5rem;
    max-width: 100%;
    margin-bottom: 2rem;
    align-items: start; /* aligns items to the start of the container */
    padding: 1rem; /* adds padding inside the container */
    background: rgba(255, 255, 255, 0.15); /* semi-transparent white background */
    backdrop-filter: blur(10px); /* blurring the background */
    border-radius: 10px; /* rounding the corners */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* subtle shadow for depth */
}

.LoanDefaultFormElement,
.LoanDefaultFormBoxElement {
    display: flex;
    flex-direction: column;
    width: 100%; /* ensures full width within the grid column */
    margin-bottom: 1rem; /* space between each form element */
}

.LoanDefaultFormElement label,
.LoanDefaultFormBoxElement label {
    font-size: 0.95rem; /* slightly smaller label font size */
    color: #fff; /* white label color for contrast */
    margin-bottom: 0.25rem; /* space between label and input */
}

.LoanDefaultFormElement input,
.LoanDefaultFormBoxElement select {
    padding: 0.5rem; /* padding inside inputs and selects */
    border: 2px solid #fff; /* white border for elements */
    border-radius: 5px; /* rounding corners of inputs and selects */
    background: none; /* transparent background */
    color: #fff; /* text color inside inputs and selects */
    font-size: 1rem; /* larger font size for better readability */
}

.button-container {
    display: flex;
    justify-content: center;
    margin-top: 1rem; /* space above the button */
}


.form-and-button-containerLD {
    display: flex;
    flex-direction: column;
    align-items: center; /* This will center the children horizontally */
}

.gold-buttonV2 {
    padding: 0.75rem 1.5rem; /* larger padding for a bigger button */
    font-size: 1.1rem; /* larger font size for the button */
    border: none; /* no border for the button */
    border-radius: 5px; /* rounding button corners */
    background: rgb(180, 102, 0); /* gold color background for the button */
    color: #000; /* black text for contrast */
    cursor: pointer; /* hand cursor on hover */
    transition: background-color 0.3s; /* smooth background color transition */
}


